body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.info-section, .info-section-sp{
    -webkit-align-content: flex-start;
            align-content: flex-start;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 20vw;
}

.info-section-sp {
    width: 55vw;
}

.info-section-headerbg {
    background-color: #1aacaf;
    border-radius: 3px;
    -webkit-flex: 2 0 100%;
            flex: 2 0 100%;
    margin-right: 2vw;
}

.info-section-header {
    color:rgb(240, 240, 240);
    -webkit-flex: 2 0 100%;
            flex: 2 0 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 110%;
    font-weight: bold;
    -webkit-order: 1;
            order: 1;
    padding-top: 2vh;
    padding-left: 2vw;
}

.info-section-category {
    color:rgb(240, 240, 240);
    -webkit-flex: 2 0 100%;
            flex: 2 0 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 60%;
    -webkit-order: 2;
            order: 2;
    padding-top: 0.5vh;
    padding-bottom: 1.6vh;
    padding-left: 2vw;
}

.info-section-gap {
    -webkit-flex: 2 0 100%;
            flex: 2 0 100%;
    padding-bottom: 3vh;
}

.info-section-info, .info-section-info-sm, .info-section-default-text {
    color:#000000;
    display: inline-block;
    -webkit-flex: 1 0 80%;
            flex: 1 0 80%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 75%;
    line-height: 2vh;
    -webkit-order: 3;
            order: 3;
    padding-right: 1vw;
    padding-left: 6%;
}

.info-section-info-sm {
    text-align: center;
    padding: 0vw;
}

.info-section-default-text {
    font-size: 60%;
    padding-top: 2vh;
    padding-left: 2vw;
}

.info-section-icon, .info-section-icon-sm {
    color:#9b9b9b;
    font-size: 120%;
    -webkit-order: 4;
            order: 4;
    padding-right: 5%;
}

.info-section-icon {
    float: left;
}

.info-section-icon-sm {
    filter: gray;
    -webkit-filter: grayscale(1);
    font-size: 180%;
    width: 10%;
}

.info-section-icon-sm:hover {
    filter: none;
    -webkit-filter: grayscale(0);
}

.info-section-hr {
    color:#9b9b9b;
    padding-right: 1.5vw;
}

a {
    color: #808080;
}
a:hover {
    color: #0000BB;
}
.filter-section, .filter-section-sp {
    -webkit-align-content: flex-start;
            align-content: flex-start;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 20vw;
}

.filter-section-sp {
    width: 20vw;
}

.filter-section-header-bg, .filter-section-header-bg-sp {
    background-color: #14C697;
    border-radius: 3px;
    -webkit-flex: 2 0 100%;
            flex: 2 0 100%;
    margin-right: 2vw;
    padding-bottom: 2vh;
}

.filter-section-headerbg {
    height: 4.5vh;
}

.filter-section-header-bg-sp {
    height: 6.5vh;
}

.filter-section-header {
    color:rgb(240, 240, 240);
    -webkit-flex: 1 0 80%;
            flex: 1 0 80%;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 110%;
    font-weight: bold;
    -webkit-order: 1;
            order: 1;
    padding-top: 2vh;
    padding-left: 2vw;
}

.filter-section-gap {
    -webkit-flex: 2 0 100%;
            flex: 2 0 100%;
    padding-bottom: 3vh;
}

.filter-list {
    background-color: #eeeeee;
    border-radius: 4px;
    color: #404040;
    padding: 1vh;
    width: 80%;
    -webkit-order: 5;
            order: 5;
}

.filter-type-dropdown, .filter-data-dropdown {
    -webkit-flex: 1 0 80%;
            flex: 1 0 80%;
    font-size: 60%;
    margin-left: 1vw;
    margin-right: 1vw;
    padding-left: 5%;
    padding-bottom: 5%;
}

.Dropdown-menu-type, .Dropdown-menu-type-sp {
    background-color: #f0f0f0;
    border-radius: 4px;
    color: #404040;
    cursor: pointer;
    min-width: 60px;
    max-height: 150px;
    overflow-y: auto;
    padding-top: 1vh;
    position: absolute;
    text-align: left;
    width: 13%;
}

.Dropdown-menu-type-sp {
    width: 30%;
}

.Dropdown-control {
    color: #404040;
    cursor: pointer;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 120%;
}

.Dropdown-option {
    box-sizing: border-box;
    color: rgba(51, 51, 51, 0.8);
    cursor: pointer;
    display: block;
    font-size: 120%;
    padding: 10px 10px;
    padding-left: 1.5vw;
  }

.Dropdown-option:hover {
    background-color: #dddddd;
    color: #333;
  }

.Dropdown-icon, .Dropdown-icon-sp {
    position: relative;
    float: right;
}

.Dropdown-icon {
    margin-top: -5%;
}

.Dropdown-icon-sp {
    margin-top: -10%;
}
